@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{font-size:0.8rem}body{overflow-x:hidden;font-family:Poppins, sans-serif !important}body::-webkit-scrollbar{width:0.7rem}body::-webkit-scrollbar-thumb{background-color:#fff;border-radius:10vw}body::-webkit-scrollbar-track{background:#00aaaa}body .mobile-nav-close{display:none}body .rounded{border-radius:0.7rem !important;overflow:hidden}body .icon{color:#fff;font-size:1.3rem}body .app{background:url(/myfolio/static/media/bg.81ce80f1.svg) center center/cover fixed;z-index:5;width:100vw;min-height:100vh}body .app nav{background-color:#fff;position:relative;overflow:visible}body .app nav .mobile-navbar{position:absolute;right:0px;top:50px;width:50%;z-index:10;transition:width 0.3s linear, height 0.3s linear}body .app .sidebar{background-color:#fff}body .app .sidebar .profile-image{-webkit-clip-path:polygon(0 15%, 100% 0%, 100% 95%, 0% 100%);clip-path:polygon(0 15%, 100% 0%, 100% 95%, 0% 100%)}body .app .sidebar .profile-information{list-style:none;font-size:0.9rem}body .app .sidebar .profile-information li{position:relative}body .app .sidebar .profile-information li::before{content:"";width:0.5rem;height:0.5rem;background-color:none;position:absolute;left:-13px;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);border:2px solid #00ff88;border-radius:50%}body .app .home .my-services .card-img-top{font-size:3rem}body .app .home .progress{height:5px;border-radius:1rem}body .app .home .progress .progress-bar{border-radius:1rem}body .app .resume .resume-icon{width:3rem;height:3rem}body .app .portfolio a.card{text-decoration:none;cursor:pointer}body .app .portfolio a.link{text-decoration:none}

